import VanillaTilt from 'vanilla-tilt'

VanillaTilt.init(document.querySelector(".started__images"), {max: 5, speed: 900});
VanillaTilt.init(document.querySelectorAll(".greeting__body li"), {max: 15, speed: 900});

const swiper = new Swiper('.swiper', {
	navigation: {
		nextEl: ".button-next",
		prevEl: ".button-prev",
	},
	breakpoints: {
		320: {
			slidesPerView: 1,
			spaceBetweenSlides: 10
		},
		499: {
			slidesPerView: 2,
			spaceBetweenSlides: 10
		},
		992: {
			slidesPerView: 3,
			spaceBetweenSlides: 10
		},
		1024: {
			slidesPerView: 4,
			spaceBetweenSlides: 10
		},
		1367: {
			slidesPerView: 5,
			spaceBetweenSlides: 24
		}
	}
});
